.home {
    padding-top: 100px;
    min-height: 60vh;
    width: 100%;

    .container {
        display: flex;
        justify-content: center;
        height: 80vh;
        align-items: center;

        img {
            width: 500px;
        }

        .text-box {
            margin: 15px;
            font-family: 'Open Sans', sans-serif;
        }
    }
}

@media screen and (max-width: 1000px) {
    .home {
        
        .container {
            flex-direction: column !important;
        }
    }
}