.contact {
    min-height: 70vh;
    width: 100%;
    margin-top: 200px;

    .container {
        height: 50vh;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;

        form {
            height: 400px;
            padding: 10px 0;
            width: 600px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background: #f7f7f7;
            border-radius: 15px;
            box-shadow: 5px 3px 3px #333;

            * {
                text-align: left !important;
                padding: 5px 0;
            }

            h1 {
                margin-bottom: 25px;
            }

            input, textarea {
                padding: 10px 25px;
                font-size: 16px;
                border: none;
                background: #e7e7e8;
                color: #333;
                margin: 10px 0;
            }

            textarea {
                resize: vertical;
            }

            button {
                padding: 15px;
                background: #C2B4D9;
                color: #f1f1f1 !important;
                outline: none;
                border: none;
                color: #333;
                width: 100px;
                font-size: 16px;
                cursor: pointer;
                border-radius: 15px;
                text-align: center !important;
            }
        }
    }
}